import React, {Component} from 'react';
import {getDate, getStrFromDate, aDay} from '../handleDate';
import {Form, Divider} from 'semantic-ui-react';

export class Filter extends Component {
  clickFilter = (e) => this.props.changeCompleted(e.target.checked);
  insertDates (from, to) {
    let arr = [];
    let dates = [];
    this.props.oldData.forEach(obj => arr.push(getDate(obj.date)));
    arr.sort((a, b) => a - b);
    from = from ? getDate(from) : arr[0];
    to = to ? getDate(to) : arr[arr.length - 1];
    for (let i = from; i <= to; i += aDay) {
      dates.push({key: i, value: getStrFromDate(new Date(i)), text: getStrFromDate(new Date(i))});
      if (i === arr[0]) this.defaultFrom = getStrFromDate(new Date(i));
      if (i === arr[arr.length - 1]) this.defaultTo = getStrFromDate(new Date(i));
    };
    return dates;
  }
  defineLimit (e, elem) {
    this[elem.name] = elem.value;
  }
  startFilter = (e) => {
    const form = document.querySelector(`#filter`);
    let from = this.from || this.defaultFrom;
    let to = this.to || this.defaultTo;
    from = getDate(from);
    to = getDate(to);
    let input = form.elements.search.value.toLowerCase();
    let data = this.props.oldData
      .filter(obj => {
        if (getDate(obj.date) >= from && getDate(obj.date) <= to) return true;
      })
      .filter(obj => {
        if (input !== ``) {
          if (obj.title.toLowerCase().indexOf(input) >= 0 || obj.description.toLowerCase().indexOf(input) >= 0) return true;
        } else {
          return true
        }
      });
      this.props.refresh(data);
  };
  render () {
    this.insertDates();
    const from = this.from || this.defaultFrom;
    const to = this.to || this.defaultTo;
    return (
      <Form inverted name="filter" id="filter">
      <Divider inverted horizontal>Filter</Divider>
        <Form.Group widths='equal'>
          <Form.Checkbox
            id='completed'
            label='Show completed'
            onClick={this.clickFilter}/>
          <Form.Select
            name='from'
            placeholder='From'
            value={from}
            options={this.insertDates(undefined, this.to)}
            onChange={(e, elem) => {this.defineLimit(e, elem); this.startFilter();}}/>
          <Form.Select
            name='to'
            placeholder='To'
            value={to}
            options={this.insertDates(this.from)}
            onChange={(e, elem) => {this.defineLimit(e, elem); this.startFilter();}}/>
        </Form.Group>
        <Form.Input
          name="search"
          placeholder='Search...'
          onInput={this.startFilter}/>
      </Form>
    );
  };
};



