import React, {Component} from 'react';
import {Form, Header, Button, Dimmer, Divider} from 'semantic-ui-react';

export class ModalWindow extends Component {
  state = {message: ``};
  saveData = () => {
    let arr = this.props.data.map(obj => {
      if (obj.key === this.props.modal.key) {
        let newObj = Object.assign({}, obj);
        newObj.description = this.state.message;
        return newObj;
      }
      return obj;
    });
    this.props.change(arr)
    this.props.hide();
  }
  render () {
    if (!this.props.modal) return null;
    const data = this.props.modal;
    return (
      <Dimmer
        inverted
        active={data.dimmer}
        onClickOutside={this.props.hide}
        page>
        <Form id='modal' widths='equal'>
          <Divider
            inverted
            horizontal>
            Change Task
          </Divider>
          <Header
            textAlign='left'
            inverted
            content={`Title: ${data.title}`}/>
          <label
            className='white'>
            Description:
          </label>
          <Form.TextArea
            defaultValue={data.message}
            onChange={e => this.setState({message: e.target.value})}/>
          <Form.Field inline>
            <Button
              color='pink'
              onClick={this.props.hide}>
              Cancel
            </Button>
            <Button
              color='teal'
              onClick={this.saveData}>
              Save
            </Button>
          </Form.Field>
        </Form>
      </Dimmer>
    );
  };
};



