import React, {Component} from 'react';
import {getStrFromDate, aDay} from '../handleDate';
import {Form, Divider} from 'semantic-ui-react';

export class NewTask extends Component {
  state = {
    priority: `1`,
    date: getStrFromDate(new Date())
  };
  dateInArr () {
    let arrDays = [];
    let count = 0;
    for (let i = 0; i < 365; i++) {
      let day = getStrFromDate(new Date(Date.now() + count));
      count += aDay;
      arrDays.push({key: i, value: day, text: day});
    }
    this.defaultDate = arrDays[0].text;
    return arrDays;
  }
  click = (e) => {
    e.preventDefault();
    let form = document.querySelector(`#add`);
    let title = document.querySelector(`#title`)
    if (title.value.trim() !== ``) {
      let obj = {};
      obj.done = false;
      obj.key = `${Date.now()}`;
      obj.title = title.value;
      title.value = ``;
      obj.priority = this.state.priority;
      obj.date = this.state.date;
      let task = form.querySelector(`#task`);
      obj.description = task.value;
      task.value = ``;
      this.props.add(obj);
      this.setState({
        priority: `1`,
        date: getStrFromDate(new Date())
      });
    }
  };
  addDataOfSelect = (e, elem) => {
    this[elem.name] = elem.value;
  }
  render () {
    const priority = [
      {key: `1`, value: `1`, text: `Hight`},
      {key: `2`, value: `2`, text: `Medium`},
      {key: `3`, value: `3`, text: `Low`}
    ];
    return (
      <Form id='add' name='add'>
        <Divider inverted horizontal>New Task</Divider>
        <Form.Group widths='equal'>
          <Form.Input
            id='title'
            name='title'
            placeholder='Title...' />
          <Form.Select
            value={this.state.priority}
            name='priority'
            options={priority}
            onChange={(e, elem) => this.setState({priority: elem.value})}/>
          <Form.Select
            value={this.state.date}
            name='date'
            options={this.dateInArr()}
            onChange={(e, elem) => this.setState({date: elem.value})}/>
        </Form.Group>
        <Form.TextArea
          id='task'
          widths='equal'
          name='description'
          placeholder='Description...'/>
        <Form.Field style={{textAlign: `right`}}>
          <Form.Button
            color='teal'
            type='submit'
            onClick={this.click}>
            Add
          </Form.Button>
        </Form.Field>
      </Form>
    );
  };
};



