import React, {Component} from 'react';
import {Message as Letter} from 'semantic-ui-react';

export class Message extends Component {
  render () {
    if (!this.props.show) return null;
    const data = this.props.show;
    return (
      <Letter
        className='message'
        style={{top: `${data.y}px`, left: `${data.x}px`, maxWidth: `${data.width}px`}}>
        <Letter.Header>
          {data.title}
        </Letter.Header>
        <p>
          {data.message}
        </p>
      </Letter>
    );
  };
};



