import React, {Component} from 'react';
import {getDate} from '../handleDate';
import {Icon, Checkbox} from 'semantic-ui-react';
import {Table as Excel} from 'semantic-ui-react';
import "../style.css";

export class Table extends Component {
  constructor (props) {
    super();
    this.arr = [`Done`, `Title`, `Priority`, `Date`];
  };
  clickThead = (e) => {
    if (e.target.closest(`.up`)) {
      this.sort(e.target.closest(`th`).id.toLowerCase(), `up`);
    };
    if (e.target.closest(`.down`)) {
      this.sort(e.target.closest(`th`).id.toLowerCase(), `down`);
    };
  };
  sort (name, dir) {
    let data = JSON.parse(JSON.stringify(this.props.data));
    if (name !== `date`) {
      data.sort((a, b) => {
        let res = a[name] <= b[name] ? 1 : -1;
        if (dir === `up`) res *= -1;
        return res;
      });
    } else {
      data.sort((a, b) => {
        let res = getDate(a[name]) >= getDate(b[name]) ? 1 : -1;
        if (dir === `up`) res *= -1;
        return res;
      });
    }
    this.props.refresh(data);
  }
  delete = (key) => {
    let arr = this.props.oldData.filter(obj => {
      if (obj.key !== key) return true;
    });
    this.props.delete(arr);
  }
  createHead () {
    let arr = [];
    this.arr.forEach(name => {
      arr.push(
        <Excel.HeaderCell
          className='theadCell'
          id={name}
          key={name}>
          <div
            className='thead'>
            <span>
              {name}
            </span>
            <span>
              <label
                className='up'>
                <Icon name='caret up' />
              </label>
              <label
                className='down'>
                <Icon name='caret down' />
              </label>
            </span>
          </div>
        </Excel.HeaderCell>
      );
    });
    return arr;
  };
  createBody () {
    let arr = [];
    this.props.data.forEach((obj) => {
      if ((!this.props.completed && !obj.done) || this.props.completed) {
        arr.push(
          <Excel.Row key={obj.key}>
            <Excel.Cell
              textAlign='center'
              width='2'>
              <Checkbox
                className='done'
                defaultChecked={obj.done}
                onChange={_ => { this.props.changeDone(obj.key) }}/>
            </Excel.Cell>
            <Excel.Cell
              onMouseOver={e => this.props.showMessage(e.target, obj.title, obj.description)}
              onMouseOut={this.props.hideMessage}
              onDoubleClick={e => this.props.showModal(obj.key, obj.title, obj.description)}>
              <div className='title'>
                {obj.title}
                <label onClick={_ =>this.delete(obj.key)}>
                  <Icon
                    className='delete'
                    name='remove circle'
                    inverted
                    color='red'/>
                </label>
              </div>
            </Excel.Cell>
            <Excel.Cell
              width='3'
              textAlign='center'>{
              (() => {
                if (obj.priority === `1`) return `Hight`;
                if (obj.priority === `2`) return `Medium`;
                if (obj.priority === `3`) return `Low`;
              })()}
            </Excel.Cell>
            <Excel.Cell
              width='3'
              textAlign='center'>
              {obj.date}
            </Excel.Cell>
          </Excel.Row>
        );
      }
    });
    return arr;
  };
  render () {
    return (
      <Excel celled inverted>
        <Excel.Header onClick={this.clickThead}>
          <Excel.Row>
            {this.createHead()}
          </Excel.Row>
        </Excel.Header>
        <Excel.Body onClick={this.clickThead}>
          {this.createBody()}
        </Excel.Body>
      </Excel>
    );
  };
};



