import React, {Component} from 'react';
import {render} from 'react-dom';
import {NewTask} from './components/newTask';
import {Filter} from './components/filter';
import {Table} from './components/table';
import {Message} from './components/message';
import {ModalWindow} from './components/modal';
import {Local} from './local';
import 'semantic-ui-css/semantic.min.css';
import "./style.css";

class App extends Component {
  constructor () {
    super();
    this.local = new Local();
    this.local.read(`data`).then(obj => {
      let data = obj === null ? [] : obj.data;
      this.setState({
        data: data || [],
        oldData: data || [],
        completed: false,
        message: false,
        modal: false
      });
    });
  };
  changeCompleted (completed) {
    this.setState({completed: completed});
  }
  refreshTable (newData) {
    this.setState({data: newData});
  }
  addData (newData) {
    let data = [...this.state.oldData, newData];
    this.setState({data: data, oldData: data});
  };
  deleteData = (newData) => {
    this.setState({data: newData, oldData: newData});
  }
  changeDone (key) {
    let arr = this.state.oldData.map(obj => {
      if (obj.key === key) obj.done = !obj.done;
      return obj;
    });
    this.setState({data: arr, oldData: arr});
  };
  showMessage (elem, title, message) {
    if (elem.matches(`.delete`)) return;
    elem = elem.closest(`td`);
    const coords = elem.getBoundingClientRect();
    const x = coords.left + window.pageXOffset;
    const y = coords.bottom + window.pageYOffset;
    const width = elem.parentNode.getBoundingClientRect().right - x;
    this.timer = setTimeout(_ => {
      this.setState({message: {x, y, width, title, message}});
    }, 30);
  };
  hideMessage = () => {
    clearTimeout(this.timer);
    this.setState({message: false});
  };
  showModal (key, title, message) {
    this.setState({modal: {dimmer: true, key, title, message}});
  }
  hideModal = () => {
    this.setState({modal: false});
  }
  componentDidUpdate () {
    this.local.write(`data`, {data: this.state.oldData});
  };
  render () {
    if (!this.state) { return (
      <div id="spinner">
        <img
          src={require('./images/logo.gif')}
          alt={`https://cdn-images-1.medium.com/max/1600/0*A4wJEoDWTjk6DGKG.`}/>
      </div>
    )} else {
      return (
        <div id="todo">
          <NewTask
            add={arg => this.addData(arg)}/>
          <Filter
            changeCompleted={arg => this.changeCompleted(arg)}
            refresh={arg => this.refreshTable(arg)}
            oldData={this.state.oldData}/>
          <Table
            completed={this.state.completed}
            refresh={arg => this.refreshTable(arg)}
            data={this.state.data}
            oldData={this.state.oldData}
            delete={arg => this.deleteData(arg)}
            changeDone={arg => this.changeDone(arg)}
            showMessage={(...arg) => this.showMessage(...arg)}
            hideMessage={this.hideMessage}
            showModal={(...arg) => this.showModal(...arg)}/>
          <Message
            show={this.state.message}/>
          <ModalWindow
            modal={this.state.modal}
            hide={this.hideModal}
            data={this.state.data}
            change={this.deleteData}/>
        </div>
      );
    };
  };
};

render(<App />, document.querySelector('#root'));



