export class Local {
  write (name, data) {
    return new Promise((resolve, reject) => {
      window.localStorage.setItem(name, JSON.stringify(data));
    });
  };
  read (name) {
    return new Promise((resolve, reject) => {
      let result = JSON.parse(window.localStorage.getItem(name));
      if (result === null) {
        result = {data: [
          {key: `1518520353709`, done: false, title: `create TodoApp`, priority: `3`, date: `12.02.2018`, description: `Not ready!`},
          {key: `1518520353711`, done: true, title: `add TodoApp`, priority: `2`, date: `16.02.2018`, description: `Almost ready`}
        ]};
      };
      setTimeout(resolve, 1000, result);
    });
  }
};



